import { ObjectStateTree, ObjectType } from '@hydraengine/shared'
import { MessageBridge } from '../../message-bridge'
import { GameObject, GameObjectOptions } from '../core/game-object'

export type RectangleNodeOptions = {
  shape: number
  width: number
  height: number
} & GameObjectOptions

export class RectangleNode extends GameObject {
  type = ObjectType.Rectangle

  #shapeId: number
  #width: number
  #height: number

  constructor(options: RectangleNodeOptions) {
    super(options)
    this.#shapeId = options.shape
    this.#width = options.width
    this.#height = options.height
  }

  get width() { return this.#width }
  set width(v) {
    if (!isNaN(v) && this.#width !== v) {
      this.#width = v

      if (this.id !== undefined && this.stateTree) {
        this.stateTree.setWidth(this.id, v)
      }
    }
  }

  get height() { return this.#height }
  set height(v) {
    if (!isNaN(v) && this.#height !== v) {
      this.#height = v

      if (this.id !== undefined && this.stateTree) {
        this.stateTree.setHeight(this.id, v)
      }
    }
  }

  protected override attachToStateTree(parentId: number, stateTree: ObjectStateTree, messageBridge: MessageBridge) {
    const id = super.attachToStateTree(parentId, stateTree, messageBridge)
    stateTree.setShapeId(id, this.#shapeId)
    stateTree.setWidth(id, this.#width)
    stateTree.setHeight(id, this.#height)
    return id
  }
}
